.login {
          width: 100%;
          height:calc(var(--vh, 1vh) * 100);
    
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          position: relative;
          background-image: url('./background-hh.jpg');
          background-position: center center;
          background-size: cover;
}

.login img{
  width: 120px;
  height: 120px;

  position: absolute;
          top:10px;
          left:45%;

}
.login h1.page-title{
  font-size: 30px;
  color:rgb(36, 35, 35);
  font-weight: 500;
  margin: 10px 0px;
}


.login .form {
          width: 50%;
          padding: 10px;
          background-color: rgb(194, 186, 186);
          opacity: 1;
          display: flex;
          flex-direction: column;
          position:relative;
          border-radius: 5px;
          box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;


}

.login .form span {
          width: 100%;
          margin: 10px 0px;
          display: flex;
          justify-content: space-between;
          font-size: 20px;
          color: rgb(0, 0, 0);
          padding: 10px;


}

.login .form span.signuplink {
          font-size: 15px;
          color: grey;

}

.login .form span.signuplink a {
          text-decoration: none;
          color: rgb(17, 150, 61);
          display: inline;

}

.login .form span.signuplink a:hover {
          font-weight: 600;
}

.login .form span input {
          width: 60%;
          outline: none;
          border: none;
          background-color: rgb(122, 121, 121);
          border-bottom: 2px solid white;
          padding: 10px;
          color: rgb(53, 52, 52);
          border-top-right-radius: 5px;
          border-top-left-radius: 5px;
}

.login .form span input:hover {
          background-color: rgb(161, 159, 159);
          color: rgb(7, 7, 7);


}

.login .form span input::placeholder {
          color: rgb(85, 81, 81);
         
}

.login .form span.button {
          width: 100%;
          justify-content: center;

}

.login .form span.button button {
          padding: 10px;
          font-size: 15px;
          background-color: rgb(19, 90, 43);
          border: none;
          border-radius: 5px;
          color: rgb(201, 200, 200);
          transition: 0.4s;
}

.login .form span.button button:hover {


          background-color: rgb(35, 145, 53);
          color: white;
          
}

.login .form span a {
          text-decoration: none;
          color: rgb(61, 61, 201);
}

.login .form span.button {
          justify-content: center;
          font-size: 10px;

}
.login .popup-warning-window{
          position:absolute;
          width:100%;
          height:auto;
          bottom:100%;
          left:0%;
          background-color: white;
        
          z-index: 15;
          box-shadow: rgba(10, 10, 10, 0.801) 0px 5px 15px;
          border-radius: 5px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 20px;
        
        }
        .popup-warning-window.hide{
          display: none;
        }
@media (max-width:600px) {
  .login{
    background-image: url('./background\(1\)-hh-responsive.jpg');
  }

          .login .form {
                    width: 80%;
          }

          .login img{
            width: 40px;
            height: 40px;
            
          
          }
          .login h1.page-title{
            font-size: 15px;
            margin: 5px 0px;
           
          }
          .login .form span.signuplink {
                    font-size: 10px;
          }

          .login .form span {
                    padding: 5px;
                    margin: 5px 0px;
          }

          .login .form span.button button {
                    font-size: 10px;
          }
          .login .form span{
                    font-size: 15px;
          }


}

