
.profile{
          width:100%;
          height:100%;
          display: flex;
          flex-direction: column;
          background: none;
          position: relative;
          margin: 0px;
          padding: 0px;

}
.profile img.prof-image{
          width:300px;
          height:300px;
          border-radius: 50%;
          border:10px solid rgb(28, 6, 104);
          position:absolute;
          top:25%;
          left:10%;
}
.profile .banner{
          width:100%;
          height:40%;
          margin: 0px;

}
.profile .profile-details{
          width:100%;
          height: 60%;
          display:flex;
          flex-direction: row;
          margin: 0px;
}
.profile .banner img{
          width:100%;
          height:100%;
          margin: 0px;

}
.profile .profile-details .about{
          width:50%;
          height:100%;
          
          display:flex;
          align-items: center;
}
.profile .profile-details .about .aboutbox{
          width: 100%;
          height: 100%;
        justify-content: center;

          display:flex;
          flex-direction: column;
          position: relative;
}
.profile .profile-details .about .aboutbox span{
          width:100%;
          padding:10px;
          color:black;


}
.profile .profile-details .about .aboutbox span.name{
          font-size: 50px;
          font-weight: 600;
          color:rgb(5, 53, 33);
         
}
.profile .profile-details .about .aboutbox span.tagline{
          font-size:15px
}
.profile .profile-details .about .aboutbox span.about-details{
          font-size: 20px;
}
.profile .profile-details .about .aboutbox span.buttons{
          display:flex;
          flex-direction: row;
          justify-content: flex-end;
          position:absolute;
          bottom:0;
          right:0;

}

.profile .profile-details .about .aboutbox span.buttons button{
          border:none;
          border-radius: 5px;
          outline: none;
          background-color:rgb(17, 150, 61);
          padding: 10px;
          
         
}
.profile .profile-details .about .aboutbox span.buttons a{
          text-decoration: none;
          color:white;
          font-size: 15px;
}
.profile .profile-details .about .aboutbox span.buttons span.followers .dropup-follower-menu2{
          position: absolute;
          right: 0;
          bottom:100%;
          width: 150px;
          height: 500px;
          background-color: white;
          border: 2px solid rgb(17, 150, 61);
          z-index: 5;
          overflow-y: scroll;
          border-radius: 5px;
          padding: 10px;

}
.profile .profile-details .about .aboutbox span.buttons span.followers .dropup-follower-menu2 span{
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          font-size: 20px;
          color:black;
          margin:5px 0px;
          align-items: center;
          
          background-color: rgb(195, 196, 197);
          border-radius: 5px;

}

.profile .profile-details .about .aboutbox span.buttons span.followers .dropup-follower-menu2 span img{
          margin:10px 10px;
          width: 40px;
          height: 40px;

          border-radius: 50%;
          
}

.profile .profile-details .about .aboutbox span.buttons span.followers .dropup-follower-menu2::-webkit-scrollbar{
          display:none;
}
.profile .profile-details .about .aboutbox span.buttons span.followers{
          padding: 10px;
          border-radius: 5px;
          font-size: 15px;
          border: none;
          background-color: rgb(17, 150, 61);
          color: white;
          margin: 0px 10px;
          position: relative;

          width: fit-content;
          cursor: pointer;
}
.profile .profile-details .about .aboutbox span.buttons span.followers .dropup-follower-menu2.none{
          display: none;
}












.profile .profile-details .posts{
          width:50%;
          height:100%;
          display: flex;
          flex-direction: column;
          position: relative;
          background-color: rgb(187, 189, 190);
          overflow-y: hidden;
          margin:0px ;
          
        
}
.profile .profile-details .posts .mypostlist{
          width:100%;
          height:90%;
          
          padding:10px;
          overflow-y: auto;
          position: relative;
          overflow-x: hidden;
          
          
          

}
.profile .profile-details .posts .mypostlist p.blank-message{
          color:rgb(87, 80, 80);
          font-size: 30px;
          width: fit-content;
          margin: auto;
}
.profile .profile-details .posts .mypostlist::-webkit-scrollbar{
          display:none;
}
.profile .profile-details .posts .post-bar{
          width:100%;
          margin: 0px;
          padding:10px;
          height:10%;
          display:flex;
          justify-content: space-between;
          
          align-items: center;
          font-size: 20px;
          color:rgb(54, 50, 50);
          border-radius: 5px;
          background-color:rgb(215, 224, 215); 
          position: relative;
         
          
         

}
.profile .profile-details .posts .post-bar span.icons{
          display: flex;
          width:60%;
          justify-content: space-evenly;
          background-color: rgb(134, 143, 151);
          border-radius: 10px;
          padding: 5px;
}
.profile .profile-details .posts .post-bar span.p{
          width: 40%;
          cursor: pointer;
}
.profile .profile-details .posts .post-bar span.icons span i{
          margin :5px;
          color:rgb(47, 48, 47);
          font-size: 25px;
          cursor: pointer;

}


.profile-details .posts .post-bar span.icons span:hover i{
          color:rgb(48, 48, 133);
          border-radius: 5px;

}
.loading{
  width:100%;
  height:100%;
  margin:0px;
 
}



